<template>
  <div>
    <van-nav-bar v-if="canBack == 1" title="售后记录" fixed style="height: 46px">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left"  @click="$toPage(-1)" />
    </van-nav-bar>
    <van-nav-bar v-if="canBack == 0" title="售后记录" fixed style="height: 46px">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left"  @click="goPersonal" />
    </van-nav-bar>
    <div style="height: 46px;"></div>
    
    <div class="tab_box" style="top: 46px;">
      <div :class="{'tab_title_activity':oIndex==0,'tab_title':true}" @click="switchOrderState(0)">全部</div>
      <div :class="{'tab_title_activity':oIndex==1,'tab_title':true}" @click="switchOrderState(1)">退货退款</div>
      <div :class="{'tab_title_activity':oIndex==2,'tab_title':true}" @click="switchOrderState(2)">仅退款</div>
      <div :class="{'tab_title_activity':oIndex==3,'tab_title':true}" @click="switchOrderState(3)">补开发票</div>
      <div class="tab_line" :style="'transform: translateX('+oIndex*100+'%)'"></div>
    </div>
    <div class="placeholder_box"></div>
    
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
        <div class="order_content_box" v-for="(item,index) in orderDataList" :key="index">
          <div class="order_num">
            <div class="order_num">申请单号{{item.asno}}</div>
            <div class="order_status_txt">{{ item.ckstatus | stateTypeFormat }}</div>
          </div>
          
          <div class="order_content">
            <div class="order_content_left">
              <div class="order_goods_img">
                <img v-lazy="item.imgURl" />
              </div>
            </div>
            
            <div class="order_content_right">
              <!-- <div class="order_goods_num">共<span></span>种药品</div> -->
              <div class="order_sum">订单号：{{item.orderno}}</div>
              <div class="order_time">申请时间：{{ item.apdata}} {{item.aptime}}</div>
              <div class="order_time">售后类型：{{ item.astype | asTypeFormat }}</div>
              <div class="show_btn">
                <div class="show_btn_1" @click="seeDetail(item)">查看详情</div>
              </div>
            </div>
          </div>
        </div>
        
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
  import { Dialog } from 'vant' 
  export default {
    data() {
      return {
        canBack:1,//控制返回
        arr: ["", "", "", "", "", "", "", "", "", "", ""], // 订单号 , 商品码, ? ,企业id, 售后类型 , ??????
        oStatusArr:['','1','2','3'],
        oIndex:0,//选项卡下标
        isLoading:false,//上拉刷新
        loading:false,//下拉加载
        pageIndex:1,//初始页码
        finished:false,//可以下拉加载
        orderDataList:[],//订单数据列表
      }
    },
    filters: {
      asTypeFormat(val) {
        let text = "";
        switch (val) {
          case 1:
            text = "退款退货";
            break;
          case 2:
            text = "仅退款";
            break;
          case 3:
            text = "补开发票";
            break;
          case 4:
            text = "补开发票";
            break;
        }
        return text;
      },
      stateTypeFormat(val) {
        let text = "";
        switch (val) {
          case -2:
            text = "取消售后";
          break;
          case -1:
            text = "拒绝售后";
            break;
          case 0:
            text = "未审核";
            break;
          case 1:
            text = "审核通过";
            break;
          case 200:
            text= "售后完成"
        }
        return text;
      }
    },
    mounted(){
      if(this.$route.query.canBack == 0){
        this.canBack = 0;
      }
    },
    methods:{
      goPersonal(){//返回个人中心
        this.$router.push({
          name: 'personal'
        })
      },
      switchOrderState(index){//切换订单状态
        this.oIndex = index;
        this.pageIndex = 1;
        this.finished = false;//解除下拉加载锁定
        this.loading = true;
        this.orderDataList = [];//清空原有商品数据
        this.getOrderListData();
      },
      onRefresh(){//上拉刷新
        this.pageIndex = 1;
        this.finished = false;//解除下拉加载锁定
        this.getOrderListData();
      },
      onLoad(){//下拉加载
        this.getOrderListData();
      },
      getOrderListData(){//获取订单列表
        this.arr[4] = this.oStatusArr[this.oIndex];
        let data = this.arr;
        JNB.query({
          serverName: 'orderServer'+this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls: 'OrderInfoModule',
          method: 'queryAsapp',
          params: data,
          page:this.pageIndex,
          callback :(data)=>{
            data = JSON.parse(data);
            if(data.code == 200){
              this.isLoading = false;//如果是上拉刷新就停止刷新
              this.loading = false;//加载完成

              if(data.data && data.data.length>0){
                this.$request_img({//获取商品图片
                  context: this,
                  arr: data.data,
                  skuName:"pdno",
                  spuName:"spu"
                })
              }
              if(data.pageNo == 1){
                this.orderDataList = data.data;
              }else{
                this.orderDataList = this.orderDataList.concat(data.data);
              }
              if(data.total/data.pageSize<=this.pageIndex){
                this.finished = true;//锁定下拉加载
              }else{
                this.pageIndex = this.pageIndex+1;
              }
            }

          },
        })
      },
      
      seeDetail(item) {
        //订单详情
        this.$router.push({
          path: "/serviceAfterSalesDetails",
          query: {
            detail: JSON.stringify(item)
          }
        });
      },
    },
  }
</script>

<style scoped>
  .tab_box{
    z-index: 5;
    height: 90px;
    display: flex;
    line-height: 90px;
    position: fixed;
    left: 0;
    right: 0;
    border-bottom: 1px solid #EEEEEE;
    background-color: #FFFFFF;
    color: #7d7e80;
  }
  .tab_title{
    flex: 1;
    text-align: center;
    font-size: 28px;
    height: 90px;
    line-height: 90px;
  }
  .tab_title_activity{
    color: #ED2F26;
  }
  .tab_line{
    position: absolute;
    bottom: 0;
    width: 25%;
    height: 4px;
    background-color: #ED2F26;
    transition-duration: 0.3s;
  }
  .placeholder_box{
    height: 91px;
  }
  .order_content_box{
    margin-top: 20px;
    background-color: #FFFFFF;
  }
  .order_num{
    height: 66px;
    line-height: 66px;
    border-bottom: 1px solid #E6E6E6;
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    font-size: 28px;
    color: #333333;
  }
  .order_status_txt{
    color: #ED2F26;
  }
  .order_content{
    display: flex;
    justify-content: space-between;
    padding: 20px;
  }
  .order_content_left{
    height: 194px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .order_goods_img img{
    display: block;
    width: 160px;
    height: 160px;
  }
  .hidden-btn{
    position: relative;
    color: #999999;
    line-height: 20px;
  }
  .hidden-btn-box{
    position: absolute;
    top: 30px;
    left: 30px;
    width: 140px;
    text-align: center;
    background-color: #FFFFFF;
    border: 1px solid #EEEEEE;
    border-top: none;
    display: none;
  }
  .hidden-btn-box_show{
    display: block;
  }
  .hidden-btn-box div{
    height: 50px;
    line-height: 50px;
    font-size: 24px;
    color: #666666;
    border-top: 1px solid #EEEEEE;
  }
  .order_content_right{
    height: 194px;
    flex: 1;
  }
  .order_goods_num{
    height: 24px;
    line-height: 24px;
    font-size: 24px;
    color: #999999;
    text-align: right;
  }
  .order_goods_num span{
    color: #F8B033;
  }
  .order_sum{
    font-size: 28px;
    color: #333333;
    line-height: 42px;
    padding-left: 30px;
  }
  .order_sum span{
    color: #F91E1E;
    font-size: 32px;
  }
  .order_time{
    margin-top: 5px;
    font-size: 28px;
    color: #333333;
    line-height: 42px;
    padding-left: 30px;
  }
  .show_btn{
    display: flex;
    justify-content: flex-end;
  }
  .show_btn_0{
    border: 1px solid #ED2F26;
    color: #ED2F26;
    height: 54px;
    line-height: 54px;
    text-align: center;
    font-size: 24px;
    border-radius: 27px;
    width: 150px;
    margin-left: 30px;
  }
  .show_btn_1{
    border: 1px solid #666666;
    color: #666666;
    height: 54px;
    line-height: 54px;
    text-align: center;
    font-size: 24px;
    border-radius: 27px;
    width: 150px;
    margin-left: 20px;
  }
</style>

